                            userSelectionSix() {
                                this.state.ToolSix === false ? 
                               (this.setState({ToolSix: !this.state.ToolSix, chosen: this.state.chosen + 1},  this.runCoinTossAnimation()))
                               : 
                               (this.setState({ToolSix: false, chosen: this.state.chosen - 1},  this.reverseCoinTossAnimation()))}

                               userSelectionSeven() {
                                this.state.ToolSeven === false ? 
                               (this.setState({ToolSeven: !this.state.ToolSeven, chosen: this.state.chosen + 1},  this.runTypeChartAnimation()))
                               : 
                               (this.setState({ToolSeven: false, chosen: this.state.chosen - 1},  this.reverseTypeChartAnimation()))}

                               userSelectionEight() {
                                this.state.ToolEight === false ? 
                               (this.setState({ToolEight: !this.state.ToolEight, chosen: this.state.chosen + 1},  this.runMultiDiceAnimation()))
                               : 
                               (this.setState({ToolEight: false, chosen: this.state.chosen - 1},  this.reverseMultiDiceAnimation()))}

                               userSelectionNine() {
                                this.state.ToolNine === false ? 
                               (this.setState({ToolNine: !this.state.ToolNine, chosen: this.state.chosen + 1},  this.runNotesAnimation()))
                               : 
                               (this.setState({ToolNine: false, chosen: this.state.chosen - 1},  this.reverseNotesAnimation()))}

        ConfirmSelection() {
            // Ternary operators to evalaute the current and initial state of chosen / unchosen components 

            // 'LifePoints Component Logic'
             this.state.ToolOne === true && this.setState({ToolOneInit: true})
             this.state.ToolOne === false && this.setState({LPUnselected: false})
             this.reverseLPAnimation()

            // 'Dice Roll Component Logic'
             this.state.ToolTwo === true && this.setState({ToolTwoInit: true})
             this.state.ToolTwo === false && this.setState({DiceUnselected: false})
             this.reverseDiceAnimation()

            // 'Counter Component Logic'
             this.state.ToolThree === true && this.setState({ToolThreeInit: true})
             this.state.ToolThree === false && this.setState({CounterUnselected: false})
             this.reverseCounterAnimation()
            
            // 'Timer Component Logic'
             this.state.ToolFour === true && this.setState({ToolFourInit: true})
             this.state.ToolFour === false && this.setState({TimerUnselected: false})
             this.reverseTimerAnimation()

            // 'Card Catalog Component Logic'
             this.state.ToolFive === true && this.setState({ToolFiveInit: true})
             this.state.ToolFive === false && this.setState({CardCatalogUnselected: false})
             this.reverseCardCatalogAnimation()

            // 'Coin Toss Component Logic'
            this.state.ToolSix === true && this.setState({ToolSixInit: true})
            this.state.ToolSix === false && this.setState({CoinTossUnselected: false})
            this.reverseCoinTossAnimation()

            // 'Type Chart Component Logic'
            this.state.ToolSeven === true && this.setState({ToolSevenInit: true})
            this.state.ToolSeven === false && this.setState({TypeChartUnselected: false})
            this.reverseTypeChartAnimation()

            // 'Type Chart Component Logic'
            this.state.ToolEight === true && this.setState({ToolEightInit: true})
            this.state.ToolEight === false && this.setState({MultiDiceUnselected: false})
            this.reverseMultiDiceAnimation()

            // 'Type Chart Component Logic'
            this.state.ToolNine === true && this.setState({ToolNineInit: true})
            this.state.ToolNine === false && this.setState({NotesUnselected: false})
            this.reverseNotesAnimation()
              
             const itemsRef = firebase.database().ref(`users/${this.state.user.uid}/chosenTools/componentsBoolean`);
             const components = { 
             lifePoints: this.state.ToolOne,
             diceRoll: this.state.ToolTwo,
             counter: this.state.ToolThree,
             timer: this.state.ToolFour,
             cardCatalog: this.state.ToolFive,
             coinToss: this.state.ToolSix,
             typeChart: this.state.ToolSeven,
             multiDice: this.state.ToolEight,
             notes: this.state.ToolNine
             }
             itemsRef.set({components});

            this.setState({chosen: 0, saveData: true})
    }

