import React , { Component } from 'react';
import LifePointsIcon from '../SVG/LifePointsIcon';
import ToolIcon from '../SVG/ToolIcon';
import DropDown from '../SVG/DropDown';
import Plus from '../SVG/Plus';
import { TimelineMax } from 'gsap';
import './style.css';
import Minus from '../SVG/Minus';

class LifePoints extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
        }
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
        this.calcValue = this.calcValue.bind(this);
        this.addSelf = this.addSelf.bind(this);
        this.subtractSelf = this.subtractSelf.bind(this);
        this.addOppn = this.addOppn.bind(this);
        this.subtractOppn = this.subtractOppn.bind(this);
        this.newGame = this.newGame.bind(this);
        this.clear = this.clear.bind(this);
    }

    componentWillReceiveProps(newProps) {
        //newProps.selectedLifePoints && console.log('Life Points Chosen:', newProps.selectedLifePoints)
        }

        componentDidMount() {
            this.toggleDDtl = new TimelineMax({paused: true});
            this.limittl = new TimelineMax({paused: true});
            this.selfCalctl = new TimelineMax({paused: true});
            this.oppnCalctl = new TimelineMax({paused: true});
            this.refreshtl = new TimelineMax({paused: true});


            const {
                toggleDDtl,
                limittl,
                selfCalctl,
                oppnCalctl,
                refreshtl
            } = this;
    

            toggleDDtl.to("#lifedropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})


            limittl.to(".c-tool-lifepoints-total", .2, {backgroundColor: "#E88641", yoyo: true, repeat: 1}, 'calc')
            .to(".c-tool-lifepoints-total", .2, {color: "#FFF", yoyo: true, repeat: 1},'calc')

            selfCalctl.to(".c-tool-lifepoints-selflife", .2, {backgroundColor: "#0084FF", yoyo: true, repeat: 3})

            oppnCalctl.to(".c-tool-lifepoints-opplife", .2, {backgroundColor: "#FF0000", yoyo: true, repeat: 3})

            refreshtl.to("#refresh", .8, {rotation: 720, transformOrigin: "55.5% 58%"})

         }

        togglePlay() {
            this.toggleDDtl.play(0, false)
           }
      
           toggleReverse() {
            this.toggleDDtl.reverse()
           }
    
           dropDownToggle() {
    
            this.state.dropDown === false ? 
            (this.setState({dropDown: !this.state.dropDown}, this.togglePlay()))
            : 
            (this.setState({dropDown: false},  this.toggleReverse()))
          }

          calcValue() {
            this.limittl.play(0, false)
        }

          addSelf() {
        this.selfCalctl.play(0, false)
        }

          subtractSelf() {
        this.selfCalctl.play(0, false)
        }

          addOppn() {
        this.oppnCalctl.play(0, false)
        }

          subtractOppn() {
        this.oppnCalctl.play(0, false)
          }

          newGame() {
              this.selfCalctl.play(0, false)
              this.oppnCalctl.play(0, false)
              this.refreshtl.play(0, false)
          }
          clear() {
            this.limittl.play(0, false)
          }

render() {
    const {
        selectedLifePoints,
        theme,
        themeBackground
    } = this.props;

    return(
    <div ref={selectedLifePoints} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<LifePointsIcon/>}/>
        <p className="c-tool-name"> Life Points </p>
        <DropDown dropID="lifedropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedLifePoints ? "d-dropdown" : "d-hidden"}/>
        {this.props.children}
        <div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>
        <div className="c-tool-lifepoints">
        <span className="c-tool-lifepoints-selflife"> {this.props.yourLife} </span>
        <span className="c-tool-lifepoints-total"> {this.props.calculating} </span>
        <span className="c-tool-lifepoints-opplife"> {this.props.opponentsLife} </span>
        
        
        <div className="c-tool-lifepoints-options">
        <Plus calcPlus={this.props.calcPlusSelf} PlusClick={this.addSelf} PlusClass="c-tool-lifepoints-options-selfplus" />
        <Minus calcMinus={this.props.calcMinusSelf} MinusClick={this.subtractSelf} MinusClass="c-tool-lifepoints-options-selfminus" />
        <Plus calcPlus={this.props.calcPlusOppn} PlusClick={this.addOppn} PlusClass="c-tool-lifepoints-options-oppnplus" />
        <Minus calcMinus={this.props.calcSubtractOppn} MinusClick={this.subtractOppn} MinusClass="c-tool-lifepoints-options-oppnminus" />
        </div>


        <div 
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 1 </span>
        </div>

        <div 
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 2 </span>
        </div>

        <div 
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 3 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 4 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 5 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 6 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 7 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>    
        <span 
        onClick={this.calcValue}
        > 8 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>        
        <span 
        onClick={this.calcValue}
        > 9 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>        
        <span 
        onClick={this.calcValue}
        > 00 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.calcValue}>
        <span 
        onClick={this.calcValue}
        > 0 </span>
        </div>

        <div
        className="c-tool-lifepoints-value"
        onClick={this.props.clear}>
        <span 
        onClick={this.clear}
        > C </span>
        </div>

        <svg 
        className="c-tool-lifepoints-value-refresh"
        onClick={this.newGame}
        width="35px" 
        height="35px" 
        viewBox="0 0 35 35">
        <g id="refresh" stroke="none" strokeWidth={1} fill="none" fillRule="evenodd">
            <g
            transform="translate(16.176143, 16.196440) rotate(-135.000000) translate(-16.176143, -16.196440) translate(0.176143, 3.196440)"
            fillRule="nonzero"
            >
            <circle
                stroke="#FFF"
                strokeWidth={4}
                cx="12.9780104"
                cy="12.9780104"
                r="10.9780104"
            />
            <polygon
                fill="#E88541"
                transform="translate(23.851883, 13.803028) rotate(-195.000000) translate(-23.851883, -13.803028) "
                points="23.8060524 7.65823121 30.3644831 19.5630082 17.3392835 19.9478241"
            />
            <polygon
                fill="#FFF"
                transform="translate(23.568731, 12.337405) rotate(-195.000000) translate(-23.568731, -12.337405) "
                points="23.5687315 7.04832388 28.8578123 17.6264856 18.2796506 17.6264856"
            />
            </g>
            <rect
            onClick={this.props.newGame}
            fill="#D8D8D8"
            fillRule="nonzero"
            opacity="0"
            x={0}
            y={0}
            width={35}
            height={35}
            />
        </g>
        </svg>


        </div>
        </div>
    </div>
        )
    }
};

export default LifePoints;
