import React , { Component } from 'react';
import './style.css';
import BackgroundSVG from '../SVG/BackgroundSVG';
import BoardGameIcon from '../../components/SVG/BoardGameIcon';
import Button from '../../components/SVG/Button';
import Next from '../../components/SVG/Next';
import { TimelineMax } from 'gsap';
import { Link } from 'react-scroll';
import { Waypoint } from 'react-waypoint';
import BGSmall from '../SVG/ResponsiveBG/BRDSmall';



class BoardGame extends Component {
    constructor(props) {
        super(props);
        this.state = {
            BRDEntered: false
        }
        }

        componentDidMount() {
            this.BRDtl = new TimelineMax({paused: true});

            const {
                BRDtl
            } = this;

            BRDtl.from("#DiceThree", 1, {transformOrigin: "50% 50%", autoAlpha: 0, rotation: -400, ease: "Elastic.easeOut"}, 'spin')
            .from("#DiceFour", 2.2, {transformOrigin: "50% 50%", autoAlpha: 0, rotation: 400, ease: "Elastic.easeOut"}, 'spin')
            .from("#DiceOne", 2.6, {transformOrigin: "50% 50%", autoAlpha: 0, rotation: -400, ease: "Elastic.easeOut"}, 'spin')
            .from("#DiceTwo", 2.8, {transformOrigin: "50% 50%", autoAlpha: 0, rotation: 400, ease: "Elastic.easeOut"}, 'spin')
            BRDtl.timeScale(3);
        }

        runAnimation = () => {
            this.BRDtl.play(0, false).delay(1);
        }

        onEnter = () => {
            const {
                BRDEntered
            } = this.state;

            if(BRDEntered) {
                return;
            }
            else {
                this.runAnimation();

                this.setState({
                    BRDEntered: true,
                });
            }
        }

        render() {
        return (
            <Waypoint onEnter={this.onEnter}>
            <div className="bg-container" id={this.props.id}>
            <BackgroundSVG bgDesignID="bgBackground" backgroundClass="bg-container-background" stroke={"#AB2020"} fill={"#CA6060"}/>
            <BGSmall/>
            <h2 className="bg-container-title"> Board Games </h2>
            <BoardGameIcon BoardGameIconClassName="bg-container-icon"/>
            <p className="bg-container-desc"> Gaming tools specifically crafted for the Board Game player, 
            need help with more unique dice rolls or recording game pieces currently in play ? 
            Check out these tools available : </p>
            <Button buttonClassName="bg-container-button" text="ENTER" colour="#FFF" buttonClick={this.props.brdEnter}/>
            <Link 
            to="section3"
            spy={true}
            smooth={true}
            offset={5}
            duration={1500}>
            <Next nextClassName="bg-container-next" leftColour="#FFF" rightColour="#FFF"/>
            </Link>
            </div>
            </Waypoint>
        )
        }
    }
export default BoardGame;