import React , { Component } from 'react';
import './style.css';
import CustomIcon from '../../components/SVG/CustomIcon';
import Button from '../../components/SVG/Button';
import NextReverse from '../../components/SVG/NextReverse';
import { TimelineMax } from 'gsap';
import {animateScroll as scroll } from "react-scroll";
import { Waypoint } from 'react-waypoint';
//import BackgroundDesign from '../SVG/BackgroundDesign';
import BackgroundSVG from '../SVG/BackgroundSVG';
import CustomSmall from '../SVG/ResponsiveBG/CustomSmall';


class Custom extends Component {
    constructor(props) {
        super(props);
        this.state = {
            CustomEntered: false
        }
        }

        componentDidMount() {
            this.Customtl = new TimelineMax({paused: true});

            const {
                Customtl
            } = this;

            Customtl.from("#StarRing", 1, {transformOrigin: "50% 50%", autoAlpha: 0, rotation: 360, scaleX: 0, scaleY: 0, ease: "Back.easeOut"}, "swing")
            .from("#MainStar", 1.2, {transformOrigin: "50% 50%", autoAlpha: 0, scaleX: 0, scaleY: 0, ease: "Back.easeOut"}, "swing")
            Customtl.timeScale(0.8);
        }

        runAnimation = () => {
            this.Customtl.play(0, false).delay(1);
        }

        onEnter = () => {
            const {
                CustomEntered
            } = this.state;

            if(CustomEntered) {
                return;
            }
            else {
                this.runAnimation();

                this.setState({
                    CustomEntered: true,
                });
            }
        }

        scrollToTop = () => {
            scroll.scrollToTop();
          };

        render() {
        return (
            <div>
            <Waypoint onEnter={this.onEnter}>
            <div id={this.props.id} className="cs-container">
            <BackgroundSVG bgDesignID="csBackground" backgroundClass="cs-container-background" stroke={"#235888"} fill={"#6097CA"}/>
            <CustomSmall/>
            <h2 className="cs-container-title"> Custom </h2>
            <CustomIcon CustomIconClassName="cs-container-icon"/>
            <p className="cs-container-desc"> Need something more specific for you gaming session? 
            Combine multiple tools from the above genres for quicker access and enable the saving 
            of data you record for multiple game sessions : </p>
            <Button buttonClassName="cs-container-button" text="ENTER" colour="#FFF" buttonClick={this.props.customEnter}/>
            <NextReverse nextClassName="cs-container-next" NextReverseClick={this.scrollToTop}/>
            </div>
            </Waypoint>
            </div>

        )
        }
    }
export default Custom;