import React , { Component } from 'react';
import './style.css';
import Return from '../../components/SVG/Return';
import LifePoints from '../../components/ToolOne';
import CardCatalog from '../../components/ToolFive';
import DiceRoll from '../../components/DiceRoll';
import CoinToss from '../../components/CoinToss';
import Counter from '../../components/Counter';
import { TimelineMax } from 'gsap';


class TCGScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {

        // Quick Access for TCG Screen State Control
            LifePointsDropDown: true,
            yourLife: 8000,
            opponentsLife: 8000,
            total: null,
            calculating: 0,
            CardCatalog: true,
            DiceRollDropDown: true,
        // Single Dice Roll Data
            roll: 20,
            diceType: 20,
            diceFill: '#B18AEB',
            CoinToss: true,
            CounterDropDown: true,
            Counter: 0,
        }
        // Counter Bindings 
        this.counterIncrement = this.counterIncrement.bind(this);
        this.counterDecrement = this.counterDecrement.bind(this);
        // TCG Bindings 
        this.calcValue = this.calcValue.bind(this);
        this.clear = this.clear.bind(this);
        this.addSelf = this.addSelf.bind(this);
        this.subtractSelf = this.subtractSelf.bind(this);
        this.addOppn = this.addOppn.bind(this);
        this.subtractOppn = this.subtractOppn.bind(this);
        this.newGame = this.newGame.bind(this);
        // Dice Roll Bindings
        this.rollDice = this.rollDice.bind(this);
        this.DFourSingle = this.DFourSingle.bind(this);
        this.DSixSingle = this.DSixSingle.bind(this);
        this.DEightSingle = this.DEightSingle.bind(this);
        this.DTwelveSingle = this.DTwelveSingle.bind(this);
        this.DTwentySingle = this.DTwentySingle.bind(this);
        }

componentDidMount() {
    
    this.TCGLoadedtl = new TimelineMax({});



    const {

        TCGLoadedtl,

    } = this;

    TCGLoadedtl.from(".c-tcg", 0.8, {x:80, transformOrigin: "50% 50%", autoAlpha: 0})

}

// Counter Functionality 

counterDecrement() {
    this.setState({Counter: this.state.Counter - 1})
}

counterIncrement() {
    this.setState({Counter: this.state.Counter + 1})
}

// Life Point Functionality 

calcValue(event) {
    {this.state.calculating === 0 ?
    this.setState({
        calculating: event.target.innerText,
    }) :
    this.setState({
        calculating: this.state.calculating + event.target.innerText,
    })
    {this.state.calculating.length > 4 &&
        this.setState({calculating: this.state.calculating})
    }
    console.log(event.target.innerText)
}

}

clear(){
    this.setState({
        calculating: 0
    });
    console.log(this.state.calculating)
}

addSelf() {
    var calcConversion = Number(this.state.calculating)
    this.setState({total: calcConversion}, () => {
    this.setState({yourLife: this.state.yourLife + this.state.total,
    total: null,
    calculating: 0
    })
})
}

subtractSelf() {
    var calcConversion = Number(this.state.calculating)
    this.setState({total: calcConversion}, () => {
    this.setState({yourLife: this.state.yourLife - this.state.total,
    total: null,
    calculating: 0
    }, () => {
        this.state.yourLife < 0 && this.setState({yourLife: 0})
    })
})
}

addOppn() {
    var calcConversion = Number(this.state.calculating)
    this.setState({total: calcConversion}, () => {
    this.setState({opponentsLife: this.state.opponentsLife + this.state.total,
    total: null,
    calculating: 0
    })
})
}

subtractOppn() {
    var calcConversion = Number(this.state.calculating)
    this.setState({total: calcConversion}, () => {
    this.setState({opponentsLife: this.state.opponentsLife - this.state.total,
    total: null,
    calculating: 0
    }, () => {
        this.state.opponentsLife < 0 && this.setState({opponentsLife: 0})
    })
})
}

newGame() {
    this.setState({yourLife: 8000, opponentsLife: 8000})
}

// Dice Functionality 

rollDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.diceType) + 1)

    this.setState({roll:result})
}

DFourSingle() {
this.setState({diceType: 4, diceFill: '#DD1600', roll: 4})
}

DSixSingle() {
this.setState({diceType: 6, diceFill: '#008DFF', roll: 6})
}

DEightSingle() {
this.setState({diceType: 8, diceFill: '#00CB0E', roll: 8})
}

DTwelveSingle() {
this.setState({diceType: 12, diceFill: '#FFDD00', roll: 12})
}

DTwentySingle(){
this.setState({diceType: 20, diceFill: '#B18AEB', roll: 20})
}


render() {
return (
<div className="c-tcg">


<div className="c-tcg-header">
<Return returnClick={this.props.return} returnClassName="c-tcg-header-return" returnStroke="#6635CA"/>
<p className="c-tcg-header-title"> TCG Selection </p>
</div>

<div className="c-tcg-tools">

<div>
<LifePoints calcSubtractOppn={this.subtractOppn} calcMinusSelf={this.subtractSelf} calcPlusOppn={this.addOppn} calcPlusSelf={this.addSelf} newGame={this.newGame} clear={this.clear} calcValue={this.calcValue} yourLife={this.state.yourLife} opponentsLife={this.state.opponentsLife} calculating={this.state.calculating} selectedLifePoints={this.state.LifePointsDropDown}/>
</div>

<div>
<CardCatalog selectedCardCatalog={this.state.CardCatalog}/>
</div>

<div>
<DiceRoll selectedDice={this.state.DiceRollDropDown}
diceFill={this.state.diceFill}
rollDice={this.rollDice}
roll={this.state.roll}

DFour={this.DFourSingle}
DSix={this.DSixSingle}
DEight={this.DEightSingle}
DTwelve={this.DTwelveSingle}
DTwenty={this.DTwentySingle}
/>
</div>

<div>
<CoinToss selectedCoinToss={this.state.CoinToss}/>
</div>

<div>
<Counter CounterValue={this.state.Counter} decrement={this.counterDecrement} increment={this.counterIncrement} selectedCounter={this.state.CounterDropDown}/>
</div>

</div>

</div>
)
}
}
export default TCGScreen;