import React , { Component } from 'react';
import DiceRollIcon from '../SVG/DiceRollIcon';
import ToolIcon from '../SVG/ToolIcon';
import { TimelineMax } from 'gsap';
import DropDown from '../SVG/DropDown';
import './style.css';
import DiceType from '../SVG/DiceTypes';

class DiceRoll extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
        }
        this.rollDice = this.rollDice.bind(this);
        this.DFour = this.DFour.bind(this);
        this.DSix = this.DSix.bind(this);
        this.DEight = this.DEight.bind(this);
        this.DTwelve = this.DTwelve.bind(this);
        this.DTwenty = this.DTwenty.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
    }
    

    componentWillReceiveProps(newProps) {
        //newProps.selectedDice && console.log('Dice Roll Chosen:', newProps.selectedDice)
    }


     componentDidMount() {
        this.dicetl = new TimelineMax({paused: true});
        this.dicePickertl = new TimelineMax({paused: true});
        this.toggleDDtl = new TimelineMax({paused: true});

        const {
            dicetl,
            dicePickertl,
            toggleDDtl,
        } = this;

        dicetl.to("#diceSVG", .8, {scaleX: '0.55', scaleY: '0.55', transformOrigin: "50% 50%", rotation: 2520}, 'Spin')  
        .to("#diceSVG", .8, {ease: "Bounce.easeOut", scaleX: '1', scaleY: '1', transformOrigin: "50% 50%"})

        dicePickertl.from("#diceSVG", .8, {ease: "Bounce.easeOut", scaleX: '0.75', scaleY: '0.75', transformOrigin: "50% 50%"})

        toggleDDtl.to("#dicedropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})
     }


     togglePlay() {
      this.toggleDDtl.play(0, false)
     }

     toggleReverse() {
      this.toggleDDtl.reverse()
     }


     dropDownToggle() {

      this.state.dropDown === false ? 
      (this.setState({dropDown: !this.state.dropDown}, this.togglePlay()))
      : 
      (this.setState({dropDown: false},  this.toggleReverse()))
    }


rollDice() {
      this.dicetl.play(0, false);
}

DFour() {
    this.dicePickertl.play(0, false);
}

DSix() {
    this.dicePickertl.play(0, false);
}

DEight() {
    this.dicePickertl.play(0, false);
}

DTwelve() {
    this.dicePickertl.play(0, false);
}

DTwenty(){
    this.dicePickertl.play(0, false);
}
      
        

render() {
    const {
        selectedDice,
        theme,
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedDice} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<DiceRollIcon/>}/>
        <p className="c-tool-name"> Dice Roll </p>
        <DropDown dropID="dicedropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedDice ? "d-dropdown" : "d-hidden"}/>

        {this.props.children}

<div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>

<div className="c-tool-diceoptions">
<DiceType diceTypeFill="#CA6060" diceClick={this.props.DFour} diceTypeClassName="c-tool-diceoptions-tab four" switchClick={this.DFour} classFillName="DFour" fillId="DFour" diceType=" D 4"/>
<DiceType diceTypeFill="#5b91fb" diceClick={this.props.DSix} diceTypeClassName="c-tool-diceoptions-tab six" switchClick={this.DSix} classFillName="DSix" fillId="DSix" diceType=" D 6"/>
<DiceType diceTypeFill="#52c48e" diceClick={this.props.DEight} diceTypeClassName="c-tool-diceoptions-tab eight" switchClick={this.DEight} classFillName="DEight" fillId="DEight" diceType=" D 8"/>
<DiceType diceTypeFill="#f7a43a" diceClick={this.props.DTwelve} diceTypeClassName="c-tool-diceoptions-tab twelve" switchClick={this.DTwelve} classFillName="DTwelve" fillId="DTwelve" diceType=" D 12"/>
<DiceType diceTypeFill="#b08aeb" diceClick={this.props.DTwenty} diceTypeClassName="c-tool-diceoptions-tab twenty" switchClick={this.DTwenty} classFillName="DTwenty" fillId="DTwenty" diceType=" D 20"/>
</div>

<div onClick={this.rollDice}>
<svg onClick={this.props.rollDice} id="diceSVG" width="138px" height="222px"  viewBox="0 0 138 153">
  <g
    transform="translate(-135.000000, -360.000000) translate(30.000000, 196.000000) translate(0.000000, 56.000000) translate(85.000000, 95.000000)"
    stroke="none"
    strokeWidth={1}
    fill="none"
    fillRule="evenodd"
  >
    <polygon
      fill="#B18AEB"
      fillRule="nonzero"
      transform="translate(84.929616, 94.326097) rotate(-108.000000) translate(-84.929616, -94.326097) "
      points="84.9296158 52.0607198 129.370063 84.3485948 112.395323 136.591474 57.4639088 136.591474 40.4891684 84.3485948"
    />
    <polygon
      fill="#C7A2FF"
      fillRule="nonzero"
      transform="translate(83.711648, 36.207913) scale(1, -1) rotate(-108.000000) translate(-83.711648, -36.207913) "
      points="83.8517161 -2.18921063 108.523543 21.2766904 91.5256649 73.5907812 58.8997527 74.6050363 60.9715012 32.2006338"
    />
    <polygon
      fill="#BE96FB"
      fillRule="nonzero"
      transform="translate(41.120500, 72.569135) scale(-1, 1) rotate(-210.000000) translate(-41.120500, -72.569135) "
      points="43.4642227 27.9011347 61.7496276 44.0733132 50.6034571 97.9506772 22.296822 117.237136 20.4913716 67.6912799"
    />
    <polygon
      fill="#A47DDD"
      fillRule="nonzero"
      transform="translate(58.110833, 135.947951) scale(-1, 1) rotate(-135.000000) translate(-58.110833, -135.947951) "
      points="59.1928821 92.9410799 77.4414914 111.350549 68.8922604 165.558205 48.3813006 178.954822 38.7801749 135.298808"
    />
    <polygon
      fill="#9A72D4"
      fillRule="nonzero"
      transform="translate(120.438619, 135.972612) rotate(-135.000000) translate(-120.438619, -135.972612) "
      points="121.531793 92.9856666 139.95974 111.226205 131.343784 165.467419 111.005714 178.959557 100.917497 135.318874"
    />
    <polygon
      fill="#BE96FB"
      fillRule="nonzero"
      transform="translate(136.623618, 71.717263) rotate(-210.000000) translate(-136.623618, -71.717263) "
      points="138.804914 26.1791318 157.110882 42.2580126 145.68108 96.0125507 120.357275 117.255395 116.136354 65.4422301"
    />
    <text
      fontFamily="PingFangSC-Semibold, PingFang SC"
      fontSize={20}
      fontWeight={500}
      letterSpacing="-0.49000001"
      fill="#B991F6"
    >
      <tspan x={131} y={81}>
        ゲ
      </tspan>
    </text>
    <text
      fontFamily="PingFangSC-Semibold, PingFang SC"
      fontSize={20}
      fontWeight={500}
      letterSpacing="-0.49000001"
      fill="#B990F6"
    >
      <tspan x={28} y={83}>
        決
      </tspan>
    </text>
    <text
      fontFamily="PingFangSC-Semibold, PingFang SC"
      fontSize={20}
      fontWeight={500}
      letterSpacing="-0.49000001"
      fill="#9D76D6"
    >
      <tspan x={46} y={144}>
        闘
      </tspan>
    </text>
    <text
      fontFamily="PingFangSC-Semibold, PingFang SC"
      fontSize={20}
      fontWeight={500}
      letterSpacing="-0.49000001"
      fill="#BC97F5"
    >
      <tspan x={78} y={40}>
        ー
      </tspan>
    </text>
    <text
      fontFamily="PingFangSC-Semibold, PingFang SC"
      fontSize={20}
      fontWeight={500}
      letterSpacing="-0.392"
      fill="#956DCE"
    >
      <tspan x={114} y={142}>
        ム
      </tspan>
    </text>
    <polygon
      fill={this.props.diceFill}
      fillRule="nonzero"
      opacity="0.572767857"
      points="86.8888922 13.2817717 47.2279675 24.5 20.8764434 66.7204502 20.8764434 112.427278 44.9220854 150.04836 89.3407867 165.646319 134.084922 150.04836 157.419063 112.427278 157.419063 66.7204502 128.159665 24.5"
    />
    <text
      fontFamily="LucidaGrande-Bold, Lucida Grande"
      fontSize={45}
      fontWeight="bold"
      letterSpacing="-1.96"
      fill="#F8FAFF"
    >
      <tspan id="diceRoll" x="88" y="95" dominantBaseline="middle" textAnchor="middle">
        {this.props.roll}
      </tspan>
    </text>
  </g>
</svg>
</div>
</div>

    </div>
        )
    }
};

export default DiceRoll;
