import React , { Component } from 'react';
import CounterIcon from '../SVG/CounterIcon';
import ToolIcon from '../SVG/ToolIcon';
import DropDown from '../SVG/DropDown';
import { TimelineMax } from 'gsap';
import './style.css';

class Notes extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
        }
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
    }

    componentWillReceiveProps(newProps) {
        //newProps.selectedNotes && console.log('Notes Chosen:', newProps.selectedNotes)
    }    
    
    componentDidMount() {
        this.toggleDDtl = new TimelineMax({paused: true});


        const {
            toggleDDtl
        } = this;

        toggleDDtl.to("#notesdropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})
     }

    togglePlay() {
        this.toggleDDtl.play(0, false)
       }
  
       toggleReverse() {
        this.toggleDDtl.reverse()
       }

       dropDownToggle() {

        this.state.dropDown === false ? 
        (this.setState({dropDown: ! this.state.dropDown}, this.togglePlay()))
        : 
        (this.setState({dropDown: false},  this.toggleReverse()))
      }

render() {
    const {
        selectedNotes,
        theme,
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedNotes} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<CounterIcon/>} />
        <p className="c-tool-name"> Notes </p>
        <DropDown dropID="notesdropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedNotes ? "d-dropdown" : "d-hidden"}/>
        {this.props.children}

<div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>

<div className="c-tool-notes">
   <label className="c-tool-notes-title" htmlFor="firstNotes"> Note Collection #1</label>
   <textarea id="firstNotes" onChange={this.props.firstNotes} value={this.props.firstNotesValue} rows="10" cols="40" maxLength="250" placeholder="Enter some notes">
   </textarea>

   <label className="c-tool-notes-title" htmlFor="secondNotes"> Note Collection #2</label>
   <textarea id="secondNotes" onChange={this.props.secondNotes} value={this.props.secondNotesValue} rows="10" cols="40" maxLength="250" placeholder="Enter some notes">
   </textarea>

   <label className="c-tool-notes-title" htmlFor="thirdNotes"> Note Collection #3</label>
   <textarea id="thirdNotes" onChange={this.props.thirdNotes} value={this.props.thirdNotesValue} rows="10" cols="40" maxLength="250" placeholder="Enter some notes">
   </textarea>
   </div>
</div>

    </div>
        )
    }
};

export default Notes;
