import React , { Component } from 'react';
import CardIcon from '../SVG/CardIcon';
import ToolIcon from '../SVG/ToolIcon';
import DropDown from '../SVG/DropDown';
import { TimelineMax } from 'gsap';
import YgoApi from '../YgoApi';
import YGOIcon from '../SVG/YGOIcon';
import PkmnApi from '../PkmnApi';
import PKMNIcon from '../SVG/PKMNIcon'
import './style.css';

class CardCatalog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
            api: 'Yugioh'
        }
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
        this.PokemonAPI = this.PokemonAPI.bind(this);
        this.YugiohAPI = this.YugiohAPI.bind(this);
    }

    componentWillReceiveProps(newProps) {
        //newProps.selectedCardCatalog && console.log("Card Catalog Chosen:", newProps.selectedCardCatalog)
    }    
    
    componentDidMount() {
        this.toggleDDtl = new TimelineMax({paused: true});


        const {
            toggleDDtl,
        } = this;

        toggleDDtl.to("#carddropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})
     }

     togglePlay() {
        this.toggleDDtl.play(0, false)
       }
  
       toggleReverse() {
        this.toggleDDtl.reverse()
       }

     dropDownToggle() {

        this.state.dropDown === false ? 
        (this.setState({dropDown: ! this.state.dropDown}, this.togglePlay()))
        : 
        (this.setState({dropDown: false},  this.toggleReverse()))
      }

    YugiohAPI() {
        this.setState({api: 'Yugioh'})
    }

    PokemonAPI() {
        this.setState({api: 'Pokemon'})
    }

    
render() {
    const {
        selectedCardCatalog,
        theme,
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedCardCatalog} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<CardIcon/>} />
        <p className="c-tool-name"> Card Database </p>
        <DropDown dropID="carddropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedCardCatalog ? "d-dropdown" : "d-hidden"}/>
        {this.props.children}
        <div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>
        <div className="c-tool-options">
        <h4 className="c-tool-options-ygotext"> YGO </h4>
        <YGOIcon YGOClass="c-tool-options-ygoicon" YGOClick={this.YugiohAPI} />
        <h4 className="c-tool-options-pkmntext"> PKMN </h4>
        <PKMNIcon PKMNClass="c-tool-options-pkmnicon" PKMNClick={this.PokemonAPI} />
        </div>
        {this.state.api === 'Yugioh' ?
        <YgoApi/>
        :
        <PkmnApi/>
        }
    </div>
    </div>
        )
    }
};

export default CardCatalog;
