import React , { Component } from 'react';
import CounterIcon from '../SVG/CounterIcon';
import ToolIcon from '../SVG/ToolIcon';
import DropDown from '../SVG/DropDown';
import { TimelineMax } from 'gsap';
import './style.css';

class Counter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            incrementCounterAnimation : 0,
            dropDown: false,
        }
        this.increment = this.increment.bind(this);
        this.decrement = this.decrement.bind(this);
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
    }

    componentWillReceiveProps(newProps) {
        //newProps.selectedCounter && console.log('Counter Chosen:', newProps.selectedCounter)
    }    
    
    componentDidMount() {
        this.coinPoptl = new TimelineMax({paused: true});
        this.toggleDDtl = new TimelineMax({paused: true});


        const {
            coinPoptl,
            toggleDDtl
        } = this;

        coinPoptl.to("#Coin", 0.15, {ease: "ease: Bounce.easeOut", transformOrigin: "50% 50%", scaleX:1.2, scaleY: 1.2}) 
        .to("#Coin", 1.2, {ease: "Elastic.easeOut", transformOrigin: "50% 50%", scaleX:1, scaleY: 1}) 

        toggleDDtl.to("#counterdropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})
     }

    increment() {
        this.coinPoptl.play(0, false)
    }

    decrement() {
        this.coinPoptl.play(0, false)
    }

    togglePlay() {
        this.toggleDDtl.play(0, false)
       }
  
       toggleReverse() {
        this.toggleDDtl.reverse()
       }

       dropDownToggle() {

        this.state.dropDown === false ? 
        (this.setState({dropDown: ! this.state.dropDown}, this.togglePlay()))
        : 
        (this.setState({dropDown: false},  this.toggleReverse()))
      }

render() {
    const {
        selectedCounter,
        theme,
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedCounter} className={`c-tool counter ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<CounterIcon/>} />
        <p className="c-tool-name"> Counter </p>
        <DropDown dropID="counterdropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedCounter ? "d-dropdown" : "d-hidden"}/>
        {this.props.children}

<div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>

    <svg width="322px" height="269px" viewBox="0 0 322 269">
  <g
    transform="translate(12.000000, 55.000000)"
    stroke="none"
    strokeWidth={1}
    fill="none"
    fillRule="evenodd"
  >
    <g id="Coin" transform="translate(74.000000, 0.000000)">
      <circle
        fill="#8C65C6"
        fillRule="nonzero"
        cx={75}
        cy={83}
        r={75}
      />
      <circle
        fill="#9A72D4"
        fillRule="nonzero"
        cx={75}
        cy={77}
        r={75}
      />
      <circle
        fill="#BE96FB"
        fillRule="nonzero"
        cx={75}
        cy={75}
        r={75}
      />
      <circle
        fill="#B78CF9"
        fillRule="nonzero"
        cx={75}
        cy={75}
        r={72}
      />
      <circle
        fill="#AF82F5"
        fillRule="nonzero"
        cx="74.5"
        cy="74.5"
        r="68.5"
      />
      <rect
        fill="#B489F5"
        fillRule="nonzero"
        transform="translate(74.500000, 74.500000) rotate(-45.000000) translate(-74.500000, -74.500000) "
        x={30}
        y={30}
        width={89}
        height={89}
      />
      <text
        fontFamily="LucidaGrande-Bold, Lucida Grande"
        fontSize={45}
        fontWeight="bold"
        letterSpacing="1.716279"
        fill="#F8FAFF"
      >
        <tspan
          dominantBaseline="middle"
          textAnchor="middle"
          x={74}
          y={80}
        >
          {this.props.CounterValue}
        </tspan>
      </text>
    </g>

    <g onClick={this.props.decrement}>
    <g id="decrement" onClick={this.decrement}
       transform="translate(0.000000, 37.000000)" 
       fillRule="nonzero">
      <polygon
        points="0 41.75 40.2037037 0.773148148 40.2037037 82.7268519"
        fill="#FF5438"
      />
      <path
        d="M30.648 41.5H15"
        stroke="#FFF"
        strokeLinecap="square"
        strokeWidth={5}
      />
    </g>
    </g>
    <g onClick={this.props.increment}>
    <g id="increment" onClick={this.increment}
      transform="translate(277.500000, 78.500000) scale(-1, 1) translate(-277.500000, -78.500000) translate(257.000000, 37.000000)"
      fillRule="nonzero"
    >
      <polygon
        points="0 41.75 40.2037037 0.773148148 40.2037037 82.7268519"
        fill="#51C48E"
      />
      <g
        transform="translate(17.000000, 34.000000)"
        stroke="#FFF"
        strokeLinecap="square"
        strokeWidth={5}
      >
        <path d="M7.8 0v16m7.848-8.5H0" />
      </g>
    </g>
  </g>
  </g>
</svg>
</div>

    </div>
        )
    }
};

export default Counter;
