import React , { Component } from 'react';
import CoinTossIcon from '../SVG/CoinTossIcon';
import ToolIcon from '../SVG/ToolIcon';
import DropDown from '../SVG/DropDown';
import { TimelineMax } from 'gsap';
import './style.css';

class CoinToss extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
            headtail: 2,
            tossResult: '',
            tossFill:"#F2F2F2",
        }
        this.toss = this.toss.bind(this);
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
    }

    componentWillReceiveProps(newProps) {
        //newProps.selectedCoinToss && console.log('Coin Toss Chosen:', newProps.selectedCoinToss)
    }    
    
    componentDidMount() {
        this.coinTosstl = new TimelineMax({paused: true});
        this.toggleDDtl = new TimelineMax({paused: true});
        this.coinTossResulttl = new TimelineMax({paused: true});


        const {
            coinTosstl,
            toggleDDtl,
            coinTossResulttl
        } = this;

        coinTosstl.to("#FancyCoin", 0.7, {transformOrigin: "50% 50%", rotation: 2880, ease: "Sine.easeOut", y: 55})
        .to("#FancyCoin", 1, {transformOrigin: "50% 50%", autoAlpha: 1, ease: "Elastic.easeOut", y: 180})

        toggleDDtl.to("#cointossdropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})

        coinTossResulttl.from("#tossResult", 1, {delay:0.75, scaleX: 0, scaleY: 0, transformOrigin: "50% 50%", ease: "Elastic.easeOut" })

     }

    toss() {
        this.coinTosstl.play(0, false)
        var result = Math.floor(Math.random() * Math.floor(this.state.headtail))
        this.coinTossResulttl.play(0, false)
        {result === 0 ? this.setState({tossResult: 'TAILS!', tossFill: "#ff5438"}) : this.setState({tossResult: 'HEADS!', tossFill: "#51c48e"})}
    }

    togglePlay() {
        this.toggleDDtl.play(0, false)
       }
  
       toggleReverse() {
        this.toggleDDtl.reverse()
       }

       dropDownToggle() {

        this.state.dropDown === false ? 
        (this.setState({dropDown: ! this.state.dropDown}, this.togglePlay()))
        : 
        (this.setState({dropDown: false},  this.toggleReverse()))
      }

render() {
    const {
        selectedCoinToss,
        theme, 
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedCoinToss} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<CoinTossIcon/>} />
        <p className="c-tool-name"> Coin Toss </p>
        <DropDown dropID="cointossdropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedCoinToss ? "d-dropdown" : "d-hidden"}/>
        {this.props.children}

<div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>

    <svg onClick={this.toss} width="322px" height="269px" viewBox="0 0 322 269">
  <g stroke="none" strokeWidth={1} fill="none" fillRule="evenodd">
    <text
      id="tossResult"
      fontFamily="LucidaGrande-Bold, Lucida Grande"
      fontSize={30}
      letterSpacing="-0.581875"
      fill={this.state.tossFill}
    >
      <tspan 
      x={158} y={80}
      dominantBaseline="middle" 
      textAnchor="middle"
      >
        {this.state.tossResult}
      </tspan>
    </text>
    <g 
      id="FancyCoin"
      transform="translate(85.000000, 113.000000)"
      fillRule="nonzero"
    >
      <path
        d="M77 43c42.526 0 74.88-5.012 74.88-11.64C151.88 24.734 119.527 19 77 19 34.474 19 1.685 24.373 1.685 31S34.475 43 77 43z"
        fill="#E2E2E2"
      />
      <path
        d="M1 11h152a1 1 0 0 1 1 1v15a8 8 0 0 1-8 8H8a8 8 0 0 1-8-8V12a1 1 0 0 1 1-1z"
        fill="#E2E2E2"
      />
      <path
        d="M77 24c42.526 0 77-5.373 77-12S119.526 0 77 0 0 5.373 0 12s34.474 12 77 12z"
        fill="#AF82F5"
      />
      <polygon fill="#E3E3E3" points="76.5 4 133 11 20 11" />
      <polygon
        fill="#9E6EE9"
        transform="translate(76.500000, 14.500000) scale(-1, -1) translate(-76.500000, -14.500000) "
        points="76.5 11 133 18 20 18"
      />
      <path
        d="M12.5 20.5v14m15.75-11v14m16-12v14m17-13v14m29.25-14v14m-14.25-14v14m30-15v14m16-16v14m17-17v14"
        stroke="#E8E8E8"
        strokeLinecap="square"
      />
    </g>
  </g>
</svg>

</div>

    </div>
        )
    }
};

export default CoinToss;
