import React , { Component } from 'react';
import StopWatchIcon from '../SVG/TimerIcon';
import ToolIcon from '../SVG/ToolIcon';
import DropDown from '../SVG/DropDown';
import { TimelineMax } from 'gsap';
import './style.css';

class StopWatch extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
            //seconds: 0
        }
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
        this.minutes = this.minutes.bind(this);
        this.second = this.second.bind(this);
        this.play = this.play.bind(this);
        this.stop = this.stop.bind(this);
        this.reset = this.reset.bind(this);

    }


    componentWillReceiveProps(newProps) {
        //newProps.selectedTimer && console.log("Timer Chosen:", newProps.selectedTimer)
    } 
    
    componentDidMount() {
        this.toggleDDtl = new TimelineMax({paused: true});
        this.playtl = new TimelineMax({paused: true});
        this.resettl = new TimelineMax({paused: true});




        const {
            toggleDDtl,
            playtl,
            resettl
        } = this;

        toggleDDtl.to("#timerdropDown", .4, {rotation: 180, transformOrigin: "50% 50%"})
        playtl.to(".c-tool-start", .3, {y:20, autoAlpha: 0}, 'start')
        .to(".switch", .4, {y:10}, 'start')
        .to(".c-tool-stop, .c-tool-resume", 0.3, {y:20, autoAlpha: 1})

        resettl.from("#watch", 0.08, {x:"-=10", yoyo:true, repeat:6})
        resettl.to("#watch", 0.1, {x:-10})
        resettl.timeScale(2);

     }


    minutes() {
        return Math.floor(this.state.seconds / 60);
    }

    second() {
        return ('0' + this.state.seconds % 60).slice(-2)
    }

    play() {
       this.playStopWatch = setInterval(function () {
        this.setState({seconds: this.state.seconds + 1})
    }.bind(this), 1000)
    this.playtl.play(0, false)
    }

    stop() {
        this.playtl.reverse()
    }

    reset() {

        this.setState({seconds: 0})

        clearInterval(this.playStopWatch)

        this.playStopWatch = setInterval(function () {
            this.setState({seconds: this.state.seconds + 1})
        }.bind(this), 1000)

        this.resettl.play(0, false)


    }
    
    togglePlay() {
        this.toggleDDtl.play(0, false)
       }
  
       toggleReverse() {
        this.toggleDDtl.reverse()
       }

       dropDownToggle() {

        this.state.dropDown === false ? 
        (this.setState({dropDown: !this.state.dropDown}, this.togglePlay()))
        : 
        (this.setState({dropDown: false},  this.toggleReverse()))
      }


render() {
    const {
        selectedTimer,
        theme,
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedTimer} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<StopWatchIcon/>}/>
        <p className="c-tool-name"> StopWatch </p>
        <DropDown dropID="timerdropDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedTimer ? "d-dropdown" : "d-hidden"}/>
        {this.props.children}
        <div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>
        <svg id="stopWatch" width="322px" height="269px" viewBox="0 0 322 269">
  <g stroke="none" strokeWidth={1} fill="none" fillRule="evenodd">
   <g id="watch">
    <path
      className="switch"
      d="M169 125v11h-13v-11h-36v-11h86v11h-37z"
      fill="#C8A4FF"
      fillRule="nonzero"
    />
    <rect
      fill="#AF82F5"
      fillRule="nonzero"
      x={69}
      y={135}
      width={187}
      height={61}
    />
    <text
      fontFamily="LucidaGrande-Bold, Lucida Grande"
      fontSize={52}
      fontWeight="bold"
      letterSpacing="-0.581875"
      fill="#FFF"
    >
      <tspan 
      x={157} 
      y={172}
      dominantBaseline="middle"
      textAnchor="middle"
      >
      {this.props.minutes} : {this.props.seconds}
      </tspan>
    </text>
    <rect
      fill="#C8A4FF"
      fillRule="nonzero"
      x={53}
      y={135}
      width={16}
      height={61}
    />
    <rect
      fill="#C8A4FF"
      fillRule="nonzero"
      x={256}
      y={135}
      width={16}
      height={61}
    />
    </g>
    <g onClick={this.props.play}>
    <g 
      onClick={this.play}
      className="c-tool-start"
      transform="translate(116.000000, 43.000000)">
      <path
        d="M8 0h57a8 8 0 0 1 8 8v24H0V8a8 8 0 0 1 8-8z"
        fill="#5DD59D"
        fillRule="nonzero"
      />
      <text
        fontFamily="LucidaGrande-Bold, Lucida Grande"
        fontSize={17}
        fontWeight="bold"
        line-spacing={18}
        letterSpacing="-1.77999997"
        fill="#F8FAFF"
      >
        <tspan x={11} y={22}>
          START
        </tspan>
      </text>
    </g>
    </g>
    <g onClick={this.props.stop}>
    <g 
      onClick={this.stop}
      className="c-tool-stop"
      transform="translate(188.100000, 43.000000)">
      <path
        d="M8 0h57a8 8 0 0 1 8 8v24H0V8a8 8 0 0 1 8-8z"
        fill="#FF5438"
        fillRule="nonzero"
      />
      <text
        fontFamily="LucidaGrande-Bold, Lucida Grande"
        fontSize={17}
        fontWeight="bold"
        line-spacing={18}
        letterSpacing="-1.77999997"
        fill="#F8FAFF"
      >
        <tspan x={16} y={22}>
          STOP
        </tspan>
      </text>
    </g>
    </g>
    
    <g onClick={this.props.reset}>
    <g 
      onClick={this.reset}
      className="c-tool-resume"
      transform="translate(44.500000, 43.000000)">
      <path
        d="M8 0h57a8 8 0 0 1 8 8v24H0V8a8 8 0 0 1 8-8z"
        fill="#F7A53A"
        fillRule="nonzero"
      />
      <text
        fontFamily="LucidaGrande-Bold, Lucida Grande"
        fontSize={17}
        fontWeight="bold"
        line-spacing={18}
        letterSpacing="-1.77999997"
        fill="#F8FAFF"
      >
        <tspan x={12} y={22}>
          RESET
        </tspan>
      </text>
    </g>
  </g>
  </g>
</svg>

        </div>
    </div>
        )
    }
}

export default StopWatch;
