import React , { Component } from 'react';
import './style.css';
import BackgroundSVG from '../SVG/BackgroundSVG';
import TCGIcon from '../../components/SVG/TCGIcon';
import Button from '../../components/SVG/Button';
import Next from '../../components/SVG/Next';
import { TimelineMax } from 'gsap';
import { Link } from 'react-scroll';
import { Waypoint } from 'react-waypoint';
import TCGSmall from '../SVG/ResponsiveBG/TCGSmall';


class TCG extends Component {
    constructor(props) {
        super(props);
        this.state = {
            TCGEntered: false
        }
        }

        componentDidMount() {
            this.TCGtl = new TimelineMax({paused: true});

            const {
                TCGtl
            } = this;

            TCGtl.from("#CardOne", 1.4, {y:-20, autoAlpha: 0, ease: "Back.easeOut", delay: 0.8})
            .from("#CardTwo", 1, {x:70, autoAlpha: 0, rotation: 25, ease: "Back.easeOut"}, 'deck')
            .from("#CardThree", 1, {x:20, autoAlpha: 0, rotation: -25, ease: "Back.easeOut"}, 'deck')
            TCGtl.timeScale(1.6);
        }

        runAnimation = () => {
            this.TCGtl.play(0, false).delay(0.5);
        }

        onEnter = () => {
            const {
                TCGEntered
            } = this.state;

            if(TCGEntered) {
                return;
            }
            else {
                this.runAnimation();

                this.setState({
                    TCGEntered: true,
                });
            }
        }


        render() {
        return (
            <Waypoint onEnter={this.onEnter}>
            <div id={this.props.id} className="tcg-container">
            <BackgroundSVG bgDesignID="tcgBackground" backgroundClass="tcg-container-background" stroke={"#451996"} fill={"#6635CA"}/>
            <TCGSmall/>
            <h2 className="tcg-container-title"> TCG </h2>
            <TCGIcon TCGIconClassName="tcg-container-icon"/>
            <p className="tcg-container-desc"> Gaming tools specifically crafted for the TCG player, 
            whether it be for keeping track of multiple dice rolls, 
            calculating mana or tracking life-points we’ve got you covered with this quick selection : </p>
            <Button buttonClassName="tcg-container-button" text="ENTER" colour="#FFF" buttonClick={this.props.tcgEnter}/>
            <Link 
            to="section2"
            spy={true}
            smooth={true}
            offset={5}
            duration={1500}>
            <Next nextClassName="tcg-container-next" leftColour="#FFF" rightColour="#FFF"/>
            </Link>
            </div>
            </Waypoint>
        )
        }
    }
export default TCG;