import React, { Component } from 'react';
import './App.css';
import CustomScreen from '../src/screens/CustomScreen';
import TCGScreen from '../src/screens/TCG';
import BoardGamesScreen from '../src/screens/BoardGamesScreen';
import RPGGamesScreen from '../src/screens/RPGScreen';
import Home from '../src/screens/Home';
class App extends Component {
  constructor(props) {
    super(props);
    this.custom = this.custom.bind(this);
    this.customReturn = this.customReturn.bind(this);
    this.tcg = this.tcg.bind(this);
    this.tcgReturn = this.tcgReturn.bind(this);
    this.brdReturn = this.brdReturn.bind(this);
    this.brd = this.brd.bind(this);
    this.rpgReturn = this.rpgReturn.bind(this);
    this.rpg = this.rpg.bind(this);

    this.state = {
      custom: false,
      tcg: false,
      board: false,
      rpg: false,
    }
    }

  custom() {

    var onlineStatus = navigator.onLine ? "online" : "offline";

    onlineStatus ===  "online" ? this.setState({custom: true}) : alert('You are currently offline, please use the quick access screens until you can secure a connection.')
}

  customReturn() {
    this.setState({custom: false})
}

  tcg() {
    this.setState({tcg: true})
}

  tcgReturn() {
    this.setState({tcg: false})
  }

  brd() {
    this.setState({board: true})
}

  brdReturn() {
  this.setState({board: false})
}

rpg() {
  this.setState({rpg: true})
}

rpgReturn() {
this.setState({rpg: false})
}



  render() {
    return (
      <div className="App">
      {this.state.custom === false && this.state.tcg === false && this.state.board === false && this.state.rpg === false &&
      <Home 
      tcgButtonClick={this.tcg} 
      customButtonClick={this.custom} 
      brdButtonClick={this.brd}
      rpgButtonClick={this.rpg}
      />
      }

      {this.state.custom === true && <CustomScreen return={this.customReturn}/>}
      {this.state.tcg === true && <TCGScreen return={this.tcgReturn}/>}
      {this.state.board === true && <BoardGamesScreen return={this.brdReturn}/>}
      {this.state.rpg === true && <RPGGamesScreen return={this.rpgReturn}/>}

      </div>
    );
  }
}

export default App;
