import React , { Component } from 'react';
import DiceRollIcon from '../SVG/DiceRollIcon';
import ToolIcon from '../SVG/ToolIcon';
import { TimelineMax } from 'gsap';
import DropDown from '../SVG/DropDown';
import Dice from '../SVG/Dice';
import './style.css';
import DiceType from '../SVG/DiceTypes';

class MultiDice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dropDown: false,
        }
        this.rollFirstDice = this.rollFirstDice.bind(this);
        this.rollSecondDice = this.rollSecondDice.bind(this);
        this.rollThirdDice = this.rollThirdDice.bind(this);
        this.rollFourthDice = this.rollFourthDice.bind(this);
        this.rollFifthDice = this.rollFifthDice.bind(this);
        this.rollSixthDice = this.rollSixthDice.bind(this);
        this.rollAll = this.rollAll.bind(this);
        this.DFour = this.DFour.bind(this);
        this.DSix = this.DSix.bind(this);
        this.DEight = this.DEight.bind(this);
        this.DTwelve = this.DTwelve.bind(this);
        this.DTwenty = this.DTwenty.bind(this);
        this.dropDownToggle = this.dropDownToggle.bind(this);
        this.togglePlay = this.togglePlay.bind(this);
        this.toggleReverse = this.toggleReverse.bind(this);
    }
    

    componentWillReceiveProps(newProps) {
        //newProps.selectedMultiDice && console.log('Dice Roll Chosen:', newProps.selectedMultiDice)
    }


     componentDidMount() {
        this.dOnetl = new TimelineMax({paused: true});
        this.dTwotl = new TimelineMax({paused: true});
        this.dThreetl = new TimelineMax({paused: true});
        this.dFourtl = new TimelineMax({paused: true});
        this.dFivetl = new TimelineMax({paused: true});
        this.dSixtl = new TimelineMax({paused: true});
        this.dicePickertl = new TimelineMax({paused: true});
        this.toggleDDtl = new TimelineMax({paused: true});

        const {
            dOnetl,
            dTwotl,
            dThreetl,
            dFourtl,
            dFivetl,
            dSixtl,
            dicePickertl,
            toggleDDtl,
        } = this;

        dOnetl.to("#first", .5, {scaleX: '1', scaleY: '1',transformOrigin: "50% 50%", rotation: 2520})
        dTwotl.to("#second", 1, {scaleX: '1', scaleY: '1',transformOrigin: "50% 50%", rotation: 2520})  
        dThreetl.to("#third", 1.5, {scaleX: '1', scaleY: '1',transformOrigin: "50% 50%", rotation: 2520})  
        dFourtl.to("#fourth", 2, {scaleX: '1', scaleY: '1',transformOrigin: "50% 50%", rotation: 5400})  
        dFivetl.to("#fifth", 2.5, {scaleX: '1', scaleY: '1',transformOrigin: "50% 50%", rotation: 5400})  
        dSixtl.to("#sixth", 3, {scaleX: '1', scaleY: '1',transformOrigin: "50% 50%", rotation: 5400})  
  

        dicePickertl.from(".c-tool-multiRoll-dice", 0.5, {ease: "Bounce.easeOut", scaleX: '0.85', scaleY: '0.85', transformOrigin: "50% 50%"})
        .to(".c-tool-multiRoll-dice", 0.5, {ease: "Bounce.easeOut", scaleX: '1', scaleY: '1', transformOrigin: "50% 50%"})

        toggleDDtl.to("#multirollDown", .4, {rotation: 180, transformOrigin: "50% 50%"})
     }


     togglePlay() {
      this.toggleDDtl.play(0, false)
     }

     toggleReverse() {
      this.toggleDDtl.reverse()
     }


     dropDownToggle() {

      this.state.dropDown === false ? 
      (this.setState({dropDown: !this.state.dropDown}, this.togglePlay()))
      : 
      (this.setState({dropDown: false},  this.toggleReverse()))
    }


rollFirstDice() {

      this.dOnetl.play(0, false);
}
rollSecondDice() {

      this.dTwotl.play(0, false);
}
rollThirdDice() {

    this.dThreetl.play(0, false);
}
rollFourthDice() {

    this.dFourtl.play(0, false);
}
rollFifthDice() {

    this.dFivetl.play(0, false);
}
rollSixthDice() {

    this.dSixtl.play(0, false);
}

rollAll() {
    this.rollFirstDice();
    this.rollSecondDice();
    this.rollThirdDice();
    this.rollFourthDice();
    this.rollFifthDice();
    this.rollSixthDice();
}

DFour() {
    this.dicePickertl.play(0, false);
}

DSix() {
    this.dicePickertl.play(0, false);
}

DEight() {
    this.dicePickertl.play(0, false);
}

DTwelve() {
    this.dicePickertl.play(0, false);
}

DTwenty(){
    this.dicePickertl.play(0, false);
}
      
        

render() {
    const {
        selectedMultiDice,
        theme,
        themeBackground,
    } = this.props;

    return(
    <div ref={selectedMultiDice} className={`c-tool ${theme}`}>
    <ToolIcon className={"c-tool-icon"} SVGData={<DiceRollIcon/>}/>
        <p className="c-tool-name"> Multi-Dice </p>
        <DropDown dropID="multirollDown" DropDownClick={this.dropDownToggle} dropdownClass={selectedMultiDice ? "d-dropdown" : "d-hidden"}/>

        {this.props.children}

<div className={this.state.dropDown === false ? "d-hidden" : `c-tool-bg ${themeBackground}`}>

<div>

<div className="c-tool-diceoptions">
<DiceType diceTypeFill="#CA6060" diceClick={this.props.DFour} diceTypeClassName="c-tool-diceoptions-tab four" switchClick={this.DFour} classFillName="DFour" fillId="DFour" diceType=" D 4"/>
<DiceType diceTypeFill="#5b91fb" diceClick={this.props.DSix} diceTypeClassName="c-tool-diceoptions-tab six" switchClick={this.DSix} classFillName="DSix" fillId="DSix" diceType=" D 6"/>
<DiceType diceTypeFill="#52c48e" diceClick={this.props.DEight} diceTypeClassName="c-tool-diceoptions-tab eight" switchClick={this.DEight} classFillName="DEight" fillId="DEight" diceType=" D 8"/>
<DiceType diceTypeFill="#f7a43a" diceClick={this.props.DTwelve} diceTypeClassName="c-tool-diceoptions-tab twelve" switchClick={this.DTwelve} classFillName="DTwelve" fillId="DTwelve" diceType=" D 12"/>
<DiceType diceTypeFill="#b08aeb" diceClick={this.props.DTwenty} diceTypeClassName="c-tool-diceoptions-tab twenty" switchClick={this.DTwenty} classFillName="DTwenty" fillId="DTwenty" diceType=" D 20"/>
</div>

</div>

<div onClick={this.rollAll} className="c-tool-multiRoll">

<Dice rollAllDice={this.props.rollAll} diceID="first" diceClass="c-tool-multiRoll-dice" multiDiceFill={this.props.multiFill} multiValue={this.props.firstDice}/>
<Dice rollAllDice={this.props.rollAll} diceID="second" diceClass="c-tool-multiRoll-dice" multiDiceFill={this.props.multiFill} multiValue={this.props.secondDice}/>
<Dice rollAllDice={this.props.rollAll} diceID="third" diceClass="c-tool-multiRoll-dice" multiDiceFill={this.props.multiFill} multiValue={this.props.thirdDice}/>
<Dice rollAllDice={this.props.rollAll} diceID="fourth" diceClass="c-tool-multiRoll-dice" multiDiceFill={this.props.multiFill} multiValue={this.props.fourthDice}/>
<Dice rollAllDice={this.props.rollAll} diceID="fifth" diceClass="c-tool-multiRoll-dice" multiDiceFill={this.props.multiFill} multiValue={this.props.fifthDice}/>
<Dice rollAllDice={this.props.rollAll} diceID="sixth" diceClass="c-tool-multiRoll-dice" multiDiceFill={this.props.multiFill} multiValue={this.props.sixthDice}/>

</div>
</div>

    </div>
        )
    }
};

export default MultiDice;
