import React , { Component } from 'react';
import './style.css';
import Return from '../../components/SVG/Return';
import { TimelineMax } from 'gsap';
import TypeChart from '../../components/TypeChart';
import StopWatch from '../../components/StopWatch';
import DiceRoll from '../../components/DiceRoll';
import CoinToss from '../../components/CoinToss';
import Counter from '../../components/Counter';
import MultiDice from '../../components/MultiDice';
import Notes from '../../components/Notes';

class RPGScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {

        // Quick Access for RPG Screen State Control
            TypeChart: true,
            MultiDiceDropDown: true,
        // Multi-Roll State Data 
            rollValue: 20,
            dType: 20,
            firstDice: 20,
            secondDice: 20,
            thirdDice: 20,
            fourthDice: 20,
            fifthDice: 20,
            sixthDice: 20,
            multiFill: "#B18AEB",
            StopWatchDropDown: true,
            Seconds: 0,
            DiceRollDropDown: true,
            // Single Dice Roll Data
            roll: 20,
            diceType: 20,
            diceFill: '#B18AEB',
            CoinToss: true,
            CounterDropDown: true,
            Counter: 0,
            NotesDropDown: true
        }
        // Counter Functionality 
        this.counterIncrement = this.counterIncrement.bind(this);
        this.counterDecrement = this.counterDecrement.bind(this);
        // StopWatch Functionality
        this.minutes = this.minutes.bind(this);
        this.second = this.second.bind(this);
        this.play = this.play.bind(this);
        this.stop = this.stop.bind(this);
        this.reset = this.reset.bind(this);
        // Notes Binding 
        this.firstNotes = this.firstNotes.bind(this);
        this.secondNotes = this.secondNotes.bind(this);
        this.thirdNotes = this.thirdNotes.bind(this);
        // Multi-Dice Bindings 
        this.rollFirstDice = this.rollFirstDice.bind(this);
        this.rollSecondDice = this.rollSecondDice.bind(this);
        this.rollThirdDice = this.rollThirdDice.bind(this);
        this.rollFourthDice = this.rollFourthDice.bind(this);
        this.rollFifthDice = this.rollFifthDice.bind(this);
        this.rollSixthDice = this.rollSixthDice.bind(this);
        this.rollAll = this.rollAll.bind(this);
        this.DFour = this.DFour.bind(this);
        this.DSix = this.DSix.bind(this);
        this.DEight = this.DEight.bind(this);
        this.DTwelve = this.DTwelve.bind(this);
        this.DTwenty = this.DTwenty.bind(this);
        // Dice Roll Bindings
        this.rollDice = this.rollDice.bind(this);
        this.DFourSingle = this.DFourSingle.bind(this);
        this.DSixSingle = this.DSixSingle.bind(this);
        this.DEightSingle = this.DEightSingle.bind(this);
        this.DTwelveSingle = this.DTwelveSingle.bind(this);
        this.DTwentySingle = this.DTwentySingle.bind(this);
        }

componentDidMount() {
    
    this.RPGLoadedtl = new TimelineMax({});
    window.scrollTo(0, 0)



    const {

        RPGLoadedtl,

    } = this;

    RPGLoadedtl.from(".c-rpg", 0.8, {x:80, transformOrigin: "50% 50%", autoAlpha: 0})

}

// Counter 
counterDecrement() {
    this.setState({Counter: this.state.Counter - 1})
}

counterIncrement() {
    this.setState({Counter: this.state.Counter + 1})
}

// StopWatch 

minutes() {
    return Math.floor(this.state.Seconds / 60);
}

second() {
    return ('0' + this.state.Seconds % 60).slice(-2)
}

play() {
this.playStopWatch = setInterval(function () {
    this.setState({Seconds: this.state.Seconds + 1})
}.bind(this), 1000)
}

stop() {
    clearInterval(this.playStopWatch)
}

reset() {

    this.setState({Seconds: 0})

    clearInterval(this.playStopWatch)

    this.playStopWatch = setInterval(function () {
        this.setState({Seconds: this.state.Seconds + 1})
    }.bind(this), 1000)
}

// Notes 

firstNotes(event) {
    this.setState({firstNotes: event.target.value}, () => console.log(this.state.firstNotes));
}

secondNotes(event) {
    this.setState({secondNotes: event.target.value}, () => console.log(this.state.secondNotes));
}

thirdNotes(event) {
    this.setState({thirdNotes: event.target.value}, () => console.log(this.state.thirdNotes));
}

    // Multi-Dice Functionality 

    rollFirstDice() {
        var result = Math.floor(Math.random() * Math.floor(this.state.dType) + 1)

        this.setState({firstDice:result})
    }
    rollSecondDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.dType) + 1)

    this.setState({secondDice:result})
    }
    rollThirdDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.dType) + 1)

    this.setState({thirdDice:result})
    }
    rollFourthDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.dType) + 1)

    this.setState({fourthDice:result})
    }
    rollFifthDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.dType) + 1)

    this.setState({fifthDice:result})
    }
    rollSixthDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.dType) + 1)

    this.setState({sixthDice:result})
    }

    rollAll() {
    this.rollFirstDice();
    this.rollSecondDice();
    this.rollThirdDice();
    this.rollFourthDice();
    this.rollFifthDice();
    this.rollSixthDice();
    }

    DFour() {
    this.setState({dType: 4, multiFill: '#DD1600', 
    firstDice: 4,
    secondDice: 4,
    thirdDice: 4,
    fourthDice: 4,
    fifthDice: 4,
    sixthDice: 4
    })
    }

    DSix() {
    this.setState({dType: 6, multiFill: '#008DFF', 
    firstDice: 6,
    secondDice: 6,
    thirdDice: 6,
    fourthDice: 6,
    fifthDice: 6,
    sixthDice: 6})
    }

    DEight() {
    this.setState({dType: 8, multiFill: '#00CB0E',
    firstDice: 8,
    secondDice: 8,
    thirdDice: 8,
    fourthDice: 8,
    fifthDice: 8,
    sixthDice: 8
    })
    }

    DTwelve() {
    this.setState({dType: 12, multiFill: '#FFDD00', 
    firstDice: 12,
    secondDice: 12,
    thirdDice: 12,
    fourthDice: 12,
    fifthDice: 12,
    sixthDice: 12
    })
    }

    DTwenty(){
    this.setState({dType: 20, multiFill: '#B18AEB', 
    firstDice: 20,
    secondDice: 20,
    thirdDice: 20,
    fourthDice: 20,
    fifthDice: 20,
    sixthDice: 20
    })
    }

// Dice Functionality 

rollDice() {
    var result = Math.floor(Math.random() * Math.floor(this.state.diceType) + 1)

    this.setState({roll:result})
}

DFourSingle() {
this.setState({diceType: 4, diceFill: '#DD1600', roll: 4})
}

DSixSingle() {
this.setState({diceType: 6, diceFill: '#008DFF', roll: 6})
}

DEightSingle() {
this.setState({diceType: 8, diceFill: '#00CB0E', roll: 8})
}

DTwelveSingle() {
this.setState({diceType: 12, diceFill: '#FFDD00', roll: 12})
}

DTwentySingle(){
this.setState({diceType: 20, diceFill: '#B18AEB', roll: 20})
}

render() {
return (
<div className="c-rpg">


<div className="c-rpg-header">
<Return returnClick={this.props.return} returnClassName="c-rpg-header-return" returnStroke="#4FC38D"/>
<p className="c-rpg-header-title"> RPG Selection </p>
</div>

<div className="c-rpg-tools">

<div>
<TypeChart selectedTypeChart={this.state.TypeChart}/>
</div>

<div>
<MultiDice selectedMultiDice={this.state.MultiDiceDropDown}
rollAll={this.rollAll}
DFour={this.DFour}
DSix={this.DSix}
DEight={this.DEight}
DTwelve={this.DTwelve}
DTwenty={this.DTwenty}

multiFill={this.state.multiFill}
firstDice={this.state.firstDice}
secondDice={this.state.secondDice}
thirdDice={this.state.thirdDice}
fourthDice={this.state.fourthDice}
fifthDice={this.state.fifthDice}
sixthDice={this.state.sixthDice}
/>
</div>

<div>
<StopWatch reset={this.reset} play={this.play} stop={this.stop} minutes={this.minutes()} seconds={this.second()} selectedTimer={this.state.StopWatchDropDown}/>
</div>

<div>
<DiceRoll selectedDice={this.state.DiceRollDropDown}
    diceFill={this.state.diceFill}
    rollDice={this.rollDice}
    roll={this.state.roll}

    DFour={this.DFourSingle}
    DSix={this.DSixSingle}
    DEight={this.DEightSingle}
    DTwelve={this.DTwelveSingle}
    DTwenty={this.DTwentySingle}
/>
</div>

<div>
<CoinToss selectedCoinToss={this.state.CoinToss}/>
</div>

<div>
<Counter CounterValue={this.state.Counter} decrement={this.counterDecrement} increment={this.counterIncrement} selectedCounter={this.state.CounterDropDown}/>
</div>

<div>
<Notes firstNotes={this.firstNotes} secondNotes={this.secondNotes} thirdNotes={this.thirdNotes} firstNotesValue={this.state.firstNotes} secondNotesValue={this.state.secondNotes} thirdNotesValue={this.state.thirdNotes} selectedNotes={this.state.NotesDropDown}/>
</div>

</div>

</div>
)
}
}
export default RPGScreen;