import React , { Component } from 'react';

class YgoApi extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoaded: false,
            cards: [],
            defaultYGO: 'https://db.ygoprodeck.com/api/v4/cardinfo.php?name=', //Dark%20Magician
            YGOPlaceholder: 'E.G. Dark Magician',
            card: null,
            cardValue: 'Dark%20Magician',
            error: false,
            errorMessage:'',
        }
        this.searchAPI = this.searchAPI.bind(this);
        this.inputCardValue = this.inputCardValue.bind(this);
    } 
    
    componentDidMount() {

        this.searchAPI()

     }


      inputCardValue(event) {
        this.setState({
            cardValue: event.target.value
        });
      }

    searchAPI() {

        if(this.state.cardValue !== null && this.state.cardValue !== ' ') {
        var apiQuery = this.state.defaultYGO + this.state.cardValue
        fetch(apiQuery).then(res => res.json())
        .then(json => {
            this.setState({
                isLoaded: true,
                cards: json,
            }) 
        if(this.state.cards.error == null) { 
        this.setState({card : this.state.cards[0][0], errorMessage: ''})
        }
        else{
        this.setState({errorMessage: 'No card matching that name was found in the database, please try again.',
        card: null})    
        }
        });
    }
}

    
render() {
    return(
        <div className="c-tool-card">
        <input className="c-tool-card-input" onChange={this.inputCardValue} placeholder={this.state.YGOPlaceholder}></input>
        <svg className="c-tool-card-search" onClick={this.searchAPI} width="40px" height="40px" viewBox="0 0 40 40">
        <g stroke="none" strokeWidth={1} fill="none" fillRule="evenodd">
            <rect fill="#6636CA" x={0} y={0} width={40} height={40} />
            <circle
            stroke="#FFF"
            strokeWidth={4}
            cx="19.5"
            cy="18.5"
            r="10.5"
            />
            <path
            d="M27.5 26.5L33 32"
            stroke="#FFF"
            strokeWidth={4}
            strokeLinecap="square"
            />
        </g>
        </svg>
        {this.state.card != null && 
        <React.Fragment>
        <h3 className="c-tool-card-cardname">{this.state.card.name}</h3>
        <img alt="Yugioh Card" className="c-tool-card-cardimage" src={this.state.card.image_url_small}/>
        <p className="c-tool-card-desc">{this.state.card.desc}</p>
        </React.Fragment>
        }

        {this.state.errorMessage !== '' &&
        <h3 className="c-tool-card-cardname">{this.state.errorMessage}</h3>
        }
        </div>
        )
    }
};

export default YgoApi;
