import React from 'react';
import { TimelineMax } from 'gsap';
import './style.css';

class Loader extends React.Component {

    componentDidMount() {
        this.loadertl = new TimelineMax({repeat: -1});

        const {
            loadertl
        } = this;

        loadertl.to("#Left", 1.8, {y: -10, x:-10, ease: "Circ.easeOut"})
        .to("#Left", 3, {y: 0, x:0, ease: "Circ.easeOut"}, 'Collision')
        .to("#Bottom", 1.8, {y: 10, ease: "Circ.easeOut"}, 'Collision')
        .to("#Bottom", 3, {y: 0, ease: "Circ.easeOut"}, 'Push')
        .to("#Right", 1.8, {y: -10, x:10, ease: "Circ.easeOut"}, 'Push')
        .to("#Right", 3, {y: 0, x:0, ease: "Circ.easeOut"}, 'Shove')
        .to("#Left", 1.8, {y: -10, x:-10, ease: "Circ.easeOut"}, 'Shove')
        .to("#Bottom", 1.8, {y: 10, ease: "Circ.easeOut"}, 'Shove')
        .to("#Loader", 3.5, {rotation:-360, transformOrigin: "50% 50%", ease: "Circ.easeOut"}, 'Reset')
        .to("#Left", 1.8, {y: 0, x:0, ease: "Circ.easeOut"}, 'Reset')
        .to("#Bottom", 1.8, {y: 0, ease: "Circ.easeOut"}, 'Reset')
        
        loadertl.timeScale(10);
    }

    render() {
    return (
    <div className="l-modal">
      
<svg
  width="303px"
  height="303px"
  viewBox="0 0 303 303"
>
  <g id="loader"
    transform="translate(106.000000, 106.000000)"
    fillRule="nonzero"
    stroke="none"
    strokeWidth={1}
    fill="none"
  >
    <polygon id="Right"
      fill="#4C7BAE"
      points="70 0 91.6506351 12.5 91.6506351 37.5 70 50 48.3493649 37.5 48.3493649 12.5"
    />
    <polygon id="Bottom"
      fill="#314964"
      points="46 41 67.6506351 53.5 67.6506351 78.5 46 91 24.3493649 78.5 24.3493649 53.5"
    />
    <polygon id="Left"
      fill="#D8D8D8"
      points="22 0 43.6506351 12.5 43.6506351 37.5 22 50 0.34936491 37.5 0.34936491 12.5"
    />
  </g>
</svg>


    </div>
)
    ;
  }
}

export default Loader;
