import React , { Component } from 'react';
import './style.css';
import BackgroundSVG from '../SVG/BackgroundSVG';
import RPGIcon from '../../components/SVG/RPGIcon';
import Button from '../../components/SVG/Button';
import Next from '../../components/SVG/Next';
import { TimelineMax } from 'gsap';
import { Link } from "react-scroll";
import { Waypoint } from 'react-waypoint';
import RPGSmall from '../SVG/ResponsiveBG/RPGSmall';


class RPG extends Component {
    constructor(props) {
        super(props);
        this.state = {
            RPGEntered: false
        }
        }

        componentDidMount() {
            this.RPGtl = new TimelineMax({paused: true});

            const {
                RPGtl
            } = this;

            RPGtl.from("#SwordOne", 5, {transformOrigin: "100% 100%", autoAlpha: 0, rotation: -200, ease: "Back.easeOut"}, "swing")
            .from("#SwordTwo", 5, {transformOrigin: "100% 100%", autoAlpha: 0, rotation: 30, ease: "Back.easeOut"}, "swing")
            .from("#Shield", 4, {autoAlpha: 0, y:-10}, "swing")
            RPGtl.timeScale(4);
        }

        runAnimation = () => {
            this.RPGtl.play(0, false).delay(1);
        }

        onEnter = () => {
            const {
                RPGEntered
            } = this.state;

            if(RPGEntered) {
                return;
            }
            else {
                this.runAnimation();

                this.setState({
                    RPGEntered: true,
                });
            }
        }

        render() {
        return (
            <Waypoint onEnter={this.onEnter}>
            <div className="rpg-container" id={this.props.id}>
            <BackgroundSVG bgDesignID="rpgBackground" backgroundClass="rpg-container-background" stroke={"#2D493C"} fill={"#4FC38D"}/>
            <RPGSmall/>
            <h2 className="rpg-container-title"> RPG </h2>
            <RPGIcon RPGIconClassName="rpg-container-icon"/>
            <p className="rpg-container-desc"> Gaming tools specifically crafted for the Role Playing Gamer, 
            these tools assist you in recording score pad data for multiple players, counters, timers and more :  </p>
            <Button buttonClassName="rpg-container-button" buttonClick={this.props.rpgEnter} text="ENTER" colour="#FFF"/>
            <Link 
            to="section4"
            spy={true}
            smooth={true}
            offset={5}
            duration={1500}>
            <Next nextClassName="rpg-container-next" leftColour="#FFF" rightColour="#FFF"/>
            </Link>
            </div>
            </Waypoint>
        )
        }
    }
export default RPG;